*{margin: 0; padding: 0;}
a{text-decoration: none;}
ul,li,ol{list-style: none;}

button{
  border: none;
  background: none;
}


@media (max-width:991px){


  .scrolled-up, .logonone{
   height: 100%;
   display: flex;
   transition: 1s; 
  }
 
  .navbar .navbar-brand img {
   width: 250%;
 }
 

  .d-flex{
    justify-content: flex-end;
  }
  }

/* nav Effect */
@media (min-width:992px){
   .scrolled-up, .logonone{  
  display: none;
 }
 .scrolled-down, .logoshow{
  height:100% ;
  display: flex;
  transition: 1s;  
}
}


#navbarScroll{
  justify-content: flex-end;
}
 .scrolled-fixed{
  position: fixed;
  top: 0;
  z-index: 5;
  width: 100%;
 }

 .navbar-nav-scroll{
  max-height: none !important;
  overflow: hidden;
 }

 

/* nav 부분 */
.navbar-expand-lg{
  background: #fff;
} 

.container-fluid{
  width: 64.5%; 
}

.navbar-brand{
  width: 8%;
}

.navbar-brand img{
  width: 100%;
}

.nav-btn{
  outline: none;
  border:none;
  color: rgb(102, 102, 102);
}

.btn-outline-success:hover,.btn-outline-success:active{
  outline: none;
  border: none;
  background: gray;
  box-shadow: none;
}

.btn-outline-primary:focus,.btn-check:focus+.btn, .btn:focus,.btn-outline-primary:focus{
  outline: none;
  border: none;
  box-shadow: none;
}

.nav-search{
  width: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%,0%);
  z-index: 5;
  background: #fff;
  padding: 2% 1% 0 1%;
  font-size: 0.8vw;
}

.nav-search-top{
  width: 64%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  color: #777;
}

.nav-search-top button{
  font-size: 1.2vw;
  padding-right: 1%;
}

.nav-search-middle{
  width: 64%;
  margin: 0 auto 2% auto;
  padding: 1% 0;
  border-bottom: 1px solid #eee;
}

.nav-search-middle input{
  width: 100%;
  border: none;
  position: relative;
  font-size: 1.5em;
  color: black;
  padding: 1% 0.5% 0.5%;
}

.nav-search-middle button{
  position: absolute;
  right:19%;
  font-size: 1.5em;
  padding: 0.5% 0.5% 0;
}

.search-recommend{
  display: flex;
  justify-content: space-between;
  align-content: center;
  width: 64%;
  margin: 0 auto;
}

.search-recommend div{
  width: 15%;
}

.search-recommend>div>p{
  text-align: center;
}

.search-recommend div div{
  width: 100%;
  height: 70%;
  padding-bottom: 10%;
  box-shadow: 2px 2px 5px #eee;
}

.search-recommend div div img{
  width: 100%;
  height: 100%;
}

.search-recommend div p{
  padding: 2%;
  line-height: 4vw;
}

.search-recommend div p:hover{
  color: gray;
}

.nav-search ul{
  width: 68%;
  margin: 0 auto;
  padding-bottom: 2%;
}

/* 상단 사이드 */
.nav-cart, .nav-my, .nav-gear{
  width: 15%;
  position: absolute;
  background: #fff;
  top: 100%;
  right: 18.7%;
  z-index: 5;
  padding: 0.5% 1%;
}

.nav-cart p:first-child{
  font-size: 2vw;
  text-align: center; 
  margin-bottom: 0.1%;
  color: lightgray; 
}

.nav-cart p:last-child{
  color: gray;
  text-align: center;
  font-size: 0.8vw;
}

.nav-my, .nav-gear{
  color: #666;
  padding: 1.5% 2%;
} 

.nav-my table tr:hover, .nav-gear table tr:hover{
  color: gray;
  cursor: pointer;
}


.nav-my table tr td p, .nav-gear table tr td p{
  width: 100%;
  font-size: 0.75vw;
  font-weight: 100;
  margin: 10% 35%;
}

.nav-gear table tr td p{
  margin: 2.5% 10%;
}

.btn-check:focus+.btn-outline-success,
.btn-outline-success:focus{
  box-shadow: none;
}

/* text 부분 */
.text1{
  text-align: center;
  font-size: 1.4vw;
  padding: 5.5% 0 0.7% 0;
}

/* swiper */
html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.swiper {
  width: 100%;
  /* height: 800px; */
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  /* object-fit: cover; */
}

.swiper-pagination-bullet-active{
  background: #666 !important;
}

:root {
  --bs--blue:#333;
  --swiper-theme-color: #333;
}

/* main img 부분 */
.main{
  width: 100%;
  display: flex;
}

.main img:hover{
  cursor: pointer;
}

.main-left{
  width: 50%;
  /* padding:  */
  display: flex;
  justify-content: space-evenly;
}

.main-left-first, .main-left-second{
  width: 47%;
}

.main-right{
  width: 50%;
  padding-right: 0.3%;
}

.main-right>.col h4{
  padding: 1.6% 0;
}


.col h4{
  padding: 4% 0;
  text-align: center;
  font-weight: 400;
  font-size: 1.2vw;
  cursor: pointer;
}

.col h4:hover{
  color: gray;
}

.col p{
  text-align: center;
}

/* read 부분 */
.read{
  width: 100%;
}

.up-down{
  width: 63.5%;
  margin: 0 auto 5%;
}

.up-down p{
  width: 43.2%;
  font-size: 1.5vw;
  font-weight: 350;
  padding: 0 1%;
}

.up,.down{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10%;
  
}

.up img, .down img{
  padding: 0 1%;
}


/* sub 부분 */
.sub{
  background: #fff;
  overflow: hidden;
  
}

.sub-top{
  background: #888;
  color: #fff;
}

.sub-top p{
  width: 63%;
  font-size: 0.8vw;
  margin: 0 auto;
  padding: 0.3% 0;
}

.sub-pc{
  width: 63%;
  margin: 0 auto;
  display: flex;
  padding: 3% 0;
}

.sub-pc-detail{
  width: 50%;
  display: flex;
}

.sub-pc-detail img:first-child{
  width: 20%;
  height: fit-content;
  padding-right: 5%;
}

.sub-pc-detail img:last-child{
  width: 80%;
  height: fit-content;
}

.sub-pc-ex{
  width: 50%;
  padding-left: 5%;
}

.sub-pc-ex >p{
  font-size: 0.8vw;
}

.sub-pc-ex >p>.fa-star{
  color: rgb(255, 217, 0);
}

.input-button{
  display: flex;
  justify-content: space-between;
  padding: 2% 0 6%;
}

.sub-input{
  width: 27%;
  background: #eee;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 5px;
  position: relative;
  font-size: 0.9vw;
}

.sub-input p{
  color: #999;
  width: 33.3%;
  text-align: center;
  position: absolute; 
  top: 25%;
  cursor: pointer;
  font-size: 1vw;
}

.sub-input p:first-child{
  left: 0;
}
.sub-input p:last-child{
  right: 0;
}

.sub-input input{
  width: 33.3%;
  background: none;
  border: none;
  overflow: hidden;
  text-align: center;
}
.sub-pc button{
  width: 70%;
  color: #fff;
  background: #333;
  border-radius: 5px;
  padding: 2.5% 0;
  font-size: 0.8vw;
}

.sub-pc button:hover{
  background: black;
}


.sub-pc-ul-first{
  display: flex;
  font-size: 0.8vw;
  padding: 0 0 1%;
}

.sub-pc-ul-first li{
  padding-right: 5%;
  cursor: pointer;
  color: #666;
}

.sub-pc-ul-first li:hover{
  color: #222;
}

.sub-pc-ul-first .fa-regular{
  margin-right: 0.5vw;
}

.sub-pc-ul-first .fa-scale-balanced::before{
  color: rgb(0, 102, 255);
  margin-right: 0.5vw;
}

.sub-pc-ul-last{
  color: #999;
  cursor: pointer;
  padding: 7% 0 1% 0;
  font-size: 0.8vw;
}

.sub-pc-ul-last hr{
  margin: 5% 0;  
}

.icons{
  background: #eee;
  color: #b8b8b8;
  text-align: center;
}

.icons i{
  padding: 1% 0.7%;
  cursor: pointer;
}

.icons i:hover{
  color: #fff;
}




/* sub slide 부분 */
.mySwiper1{
  overflow: visible !important;

}


.slide-button{
  width: 66%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding: 2% 0% 0.5%;
  position: relative;
}

.slide-button p{
  font-size: 1vw;
}

.slide-button-btn{
  width: 10%;
  display: flex;
  justify-content: space-between;
}

/* .slide-button-btn button */
.sub .swiper-button-prev, .sub .swiper-button-next{
  width: 1.8vw;
  height: 1.8vw;
  color: #fff;
  background: #777;
  /* padding: 5% 12%; */
  border-radius: 50%;
  position: absolute;
  top: -6.6%;
}

.sub .swiper-button-prev{
  left: 78%;
  right: 10%;
}

.sub .swiper-button-next{
  right: 17.1%;
}

.sub .swiper-button-next:after, .sub .swiper-button-prev:after{
  font-size: 1vw;

}
.sub .swiper-horizontal{
  width: 100%;
}

.sub .swiper-slide{
  padding-bottom: 2%;
}

.swiper-wrapper .main-right{
  width: 100%;
}

.col-div{
  height: 16.5svw;
}

.swiper-horizontal .col h4{
  font-size: 0.9vw;
  margin: 4% 0 3% 0;
}

.swiper-horizontal .col p{
  font-size: 1vw;
}

/* footer 부분*/
.bottom{
  width: 100%;
  background: #303030;
  color: #888;
  
}

.Footer{
  display: flex;
  justify-content: space-between;
  width: 66%;
  padding: 3vw 0 1.5vw 0;
  margin: 0 auto;
}


.Footer>ul>li a{
  color: #888;
}

.Footer>ul>li:first-child{
  padding-bottom: 18%;
}

.Footer>ul>li:first-child a{
  color: #fff;
}

.footer-one{
  list-style: none;
}

.footer-one li, .footer-two li{
  padding-bottom: 0.4vw;
}

.footer-three h6{
  color: #fff;
  font-weight: 400;
}

.footer-three{
  width: 50%;
  height: 11vw;
  padding-right: 3%;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

.search{
  width: 100%;}

.search input{
  width: 82%;
  height: 2.2vw;
  color: #888;
  padding: 1.3%;
  border: none;
  position: relative;
}

.search button{
  width: 5.5%;
  height: 2.2vw;
  background: #888;
  color: #fff;
  padding: 0.4%;
  border-radius: 0 5px 5px 0;
  position: absolute;
  font-size: 0.8vw;
}

.footer-icon{
  width: 100%;
}

.footer-icon i{
  padding-right: 4%;
}

.last{
  width: 100%;
  background: #222;
  color: #888;
  padding: 0.6vw 0 0.5vw 0;
}

.last p{
  margin-left: 2.5%;
  line-height: 0.1vw;
}

.fa-heart:before {
  color: tomato;
}
.nav-my table tr td .fa-heart:before{
  color: #666;
}

.last1{
  width: 66%; 
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.last-bar{
  display: flex;
  justify-content:flex-start;
  align-items: flex-end;
}

.last-bar a{
  width: 10%;
  margin: 0.5% 0 0.5% 3.3%;
}

.last-bar a img{
  width: 100%;
}

.last-bar1{
    margin: 0.5% 3% 0 0;
}

.last-bar1 img{
  width: 100%;

}


/* cart 부분 */
.cart2{
  text-align: center;
}

.cart-btn-div{
  width: 100%;
  display: flex;
  justify-content: center;
}

.cart-btn{
  font-size: 0.8vw;
  color:#fff;
  border: none;
  background: #333;
  margin: 1% 0 15%;
}

.cart-btn:hover{
  background: #666;
}

:root {
  --swiper-theme-color: #red !important;
}

/* Login 부분 */
.login-back{
  width: 100%;  
  background: #fff;
  padding: 10% auto;
}

.login{
  width: 45%;
  margin: 0 auto;
  padding: 8% 0 5%;
}

.login h3, .login p{
  text-align: center;
  padding-bottom: 3%;
}

.login input{
  width: 100%;
  padding: 1.8%;
  margin: 2% ;
}

.login button{
 width: 40%;
 margin: 0 auto;
 background: #333;
 color: #fff;
 border-radius: 5px;
 padding: 1%;
 margin: 5% 30%;
 font-size: 1vw;
}

.login button:hover{
  background: #888;
  transition: all 0.3s;
}

.login p{
  cursor: pointer;
  font-size: 0.9vw;
}

.login p:hover{
  color: #666;
}
